'use client'

import { useState } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

interface Hotel {
  id: string
  name: string
  location: string
  rating: number
  price: number
  originalPrice?: number
  image: string
  amenities: string[]
  description: string
  roomType: string
  cancellation: string
}

interface HotelResultsProps {
  hotels: Hotel[]
}

export default function HotelResults({ hotels }: HotelResultsProps) {
  const [sortBy, setSortBy] = useState('price')

  // Sort hotels
  const sortedHotels = [...hotels].sort((a, b) => {
    switch (sortBy) {
      case 'price':
        return a.price - b.price
      case 'rating':
        return b.rating - a.rating
      case 'name':
        return a.name.localeCompare(b.name)
      default:
        return 0
    }
  })

  const handleBookHotel = (hotel: Hotel) => {
    // Handle hotel booking logic here
    console.log('Booking hotel:', hotel)
    alert(`Booking ${hotel.name} for $${hotel.price} per night`)
  }

  const renderStars = (rating: number) => {
    return Array.from({ length: 5 }, (_, i) => (
      <svg
        key={i}
        className={`h-4 w-4 ${i < rating ? 'text-yellow-400 fill-current' : 'text-gray-300'}`}
        viewBox="0 0 20 20"
      >
        <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
      </svg>
    ))
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <h3 className="text-xl font-semibold text-slate-900">
          {sortedHotels.length} hotels found
        </h3>
        
        <div className="flex items-center space-x-2">
          <span className="text-sm text-slate-600">Sort by:</span>
          <Select value={sortBy} onValueChange={setSortBy}>
            <SelectTrigger className="w-32">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="price">Price</SelectItem>
              <SelectItem value="rating">Rating</SelectItem>
              <SelectItem value="name">Name</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      <div className="space-y-4">
        {sortedHotels.map((hotel) => (
          <Card key={hotel.id} className="hover:shadow-lg transition-shadow">
            <CardContent className="p-0">
              <div className="flex flex-col lg:flex-row">
                {/* Hotel Image */}
                <div className="lg:w-1/3">
                  <img
                    src={hotel.image}
                    alt={hotel.name}
                    className="w-full h-48 lg:h-full object-cover rounded-l-lg"
                  />
                </div>

                {/* Hotel Info */}
                <div className="flex-1 p-6">
                  <div className="flex flex-col lg:flex-row lg:items-start justify-between gap-4">
                    <div className="flex-1 space-y-3">
                      <div>
                        <h3 className="text-xl font-bold text-slate-900">{hotel.name}</h3>
                        <p className="text-sm text-slate-500">{hotel.location}</p>
                      </div>

                      <div className="flex items-center space-x-2">
                        <div className="flex items-center">
                          {renderStars(hotel.rating)}
                        </div>
                        <span className="text-sm text-slate-600">({hotel.rating}/5)</span>
                      </div>

                      <p className="text-sm text-slate-600 line-clamp-2">{hotel.description}</p>

                      <div className="flex flex-wrap gap-2">
                        {hotel.amenities.slice(0, 4).map((amenity, index) => (
                          <Badge key={index} variant="secondary" className="text-xs">
                            {amenity}
                          </Badge>
                        ))}
                        {hotel.amenities.length > 4 && (
                          <Badge variant="outline" className="text-xs">
                            +{hotel.amenities.length - 4} more
                          </Badge>
                        )}
                      </div>

                      <div className="space-y-1">
                        <p className="text-sm font-medium text-slate-700">{hotel.roomType}</p>
                        <p className="text-xs text-green-600">{hotel.cancellation}</p>
                      </div>
                    </div>

                    {/* Price and Book Button */}
                    <div className="lg:text-right space-y-3 lg:min-w-[200px]">
                      <div>
                        {hotel.originalPrice && (
                          <p className="text-sm text-slate-500 line-through">
                            ${hotel.originalPrice}
                          </p>
                        )}
                        <p className="text-2xl font-bold text-slate-900">${hotel.price}</p>
                        <p className="text-sm text-slate-500">per night</p>
                      </div>
                      
                      <Button 
                        onClick={() => handleBookHotel(hotel)}
                        className="w-full bg-black hover:bg-slate-800 text-white"
                      >
                        Select Room
                      </Button>
                      
                      <Button 
                        variant="outline" 
                        className="w-full text-sm"
                      >
                        View Details
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {sortedHotels.length === 0 && (
        <Card>
          <CardContent className="p-8 text-center">
            <p className="text-slate-500">No hotels found matching your criteria.</p>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
