'use client'

import { useState } from 'react'
import FlightResults from './FlightResults'

export default function FlightSearchForm() {
  const [searchResults, setSearchResults] = useState<any[]>([])
  const [isSearching, setIsSearching] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [debugInfo, setDebugInfo] = useState<any>(null)
  const [formData, setFormData] = useState({
    tripType: 'roundTrip',
    departure: '',
    destination: '',
    departDate: '',
    returnDate: '',
    adults: 1,
    children: 0,
    infants: 0,
    fareClass: 'Economy'
  })

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value, type } = e.target
    setFormData(prev => ({ 
      ...prev, 
      [name]: type === 'number' ? parseInt(value) || 0 : value 
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSearching(true)
    setError(null)
    setDebugInfo(null)
    
    try {
      // Map form data to match Trexedia API expectations
      const searchPayload = {
        departure: formData.departure,
        destination: formData.destination,
        departDate: formData.departDate,
        returnDate: formData.tripType === 'roundTrip' ? formData.returnDate : formData.departDate,
        tripType: formData.tripType,
        adults: formData.adults,
        children: formData.children,
        infants: formData.infants,
        fareClass: formData.fareClass,
      }

      console.log('Sending search payload:', searchPayload)

      const response = await fetch('/api/booking/flights', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(searchPayload),
      })

      const results = await response.json()
      console.log('Search results:', results)

      if (!response.ok) {
        throw new Error(results.error || 'Failed to search flights')
      }

      if (results.success) {
        setSearchResults(results.flights || [])
        setDebugInfo({
          apiConfig: results.apiConfig,
          searchCriteria: results.searchCriteria,
          rawResponseCount: results.rawResponse?.searchResults?.length || 0
        })
      } else {
        throw new Error(results.error || 'Search failed')
      }
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to search flights. Please try again.'
      setError(errorMessage)
      console.error('Flight search error:', err)
    } finally {
      setIsSearching(false)
    }
  }

  return (
    <div className="space-y-8">
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Trip Type */}
        <div className="space-y-3">
          <label className="text-base font-medium text-slate-700">Trip Type</label>
          <div className="flex space-x-6">
            <label className="flex items-center space-x-2">
              <input
                type="radio"
                name="tripType"
                value="roundTrip"
                checked={formData.tripType === 'roundTrip'}
                onChange={handleInputChange}
                className="w-4 h-4 text-black border-slate-300 focus:ring-black"
              />
              <span>Round Trip</span>
            </label>
            <label className="flex items-center space-x-2">
              <input
                type="radio"
                name="tripType"
                value="oneWay"
                checked={formData.tripType === 'oneWay'}
                onChange={handleInputChange}
                className="w-4 h-4 text-black border-slate-300 focus:ring-black"
              />
              <span>One Way</span>
            </label>
          </div>
        </div>

        {/* From and To Cities */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="space-y-2">
            <label htmlFor="departure" className="text-sm font-medium text-slate-700">From</label>
            <select
              id="departure"
              name="departure"
              value={formData.departure}
              onChange={handleInputChange}
              required
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            >
              <option value="">Select departure city</option>
              <option value="DEL">Delhi (DEL)</option>
              <option value="BOM">Mumbai (BOM)</option>
              <option value="MAA">Chennai (MAA)</option>
              <option value="BLR">Bangalore (BLR)</option>
              <option value="HYD">Hyderabad (HYD)</option>
              <option value="CCU">Kolkata (CCU)</option>
              <option value="AMD">Ahmedabad (AMD)</option>
              <option value="PNQ">Pune (PNQ)</option>
              <option value="GOI">Goa (GOI)</option>
              <option value="COK">Kochi (COK)</option>
              <option value="TRV">Trivandrum (TRV)</option>
              <option value="IXM">Madurai (IXM)</option>
              <option value="CJB">Coimbatore (CJB)</option>
              <option value="TRZ">Tiruchirapalli (TRZ)</option>
              <option value="IXE">Mangalore (IXE)</option>
              <option value="VGA">Vijayawada (VGA)</option>
              <option value="VTZ">Visakhapatnam (VTZ)</option>
              <option value="NAG">Nagpur (NAG)</option>
              <option value="RPR">Raipur (RPR)</option>
              <option value="BHO">Bhopal (BHO)</option>
              <option value="IDR">Indore (IDR)</option>
              <option value="JAI">Jaipur (JAI)</option>
              <option value="UDR">Udaipur (UDR)</option>
              <option value="JDH">Jodhpur (JDH)</option>
              <option value="IXC">Chandigarh (IXC)</option>
              <option value="ATQ">Amritsar (ATQ)</option>
              <option value="SXR">Srinagar (SXR)</option>
              <option value="LEH">Leh (LEH)</option>
              <option value="IXL">Leh (IXL)</option>
              <option value="GAU">Guwahati (GAU)</option>
              <option value="IMP">Imphal (IMP)</option>
              <option value="AJL">Aizawl (AJL)</option>
              <option value="AGX">Agatti (AGX)</option>
              <option value="IXZ">Port Blair (IXZ)</option>
            </select>
            <p className="text-xs text-slate-500">Select your departure city</p>
          </div>
          
          <div className="space-y-2">
            <label htmlFor="destination" className="text-sm font-medium text-slate-700">To</label>
            <select
              id="destination"
              name="destination"
              value={formData.destination}
              onChange={handleInputChange}
              required
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            >
              <option value="">Select destination city</option>
              <option value="DEL">Delhi (DEL)</option>
              <option value="BOM">Mumbai (BOM)</option>
              <option value="MAA">Chennai (MAA)</option>
              <option value="BLR">Bangalore (BLR)</option>
              <option value="HYD">Hyderabad (HYD)</option>
              <option value="CCU">Kolkata (CCU)</option>
              <option value="AMD">Ahmedabad (AMD)</option>
              <option value="PNQ">Pune (PNQ)</option>
              <option value="GOI">Goa (GOI)</option>
              <option value="COK">Kochi (COK)</option>
              <option value="TRV">Trivandrum (TRV)</option>
              <option value="IXM">Madurai (IXM)</option>
              <option value="CJB">Coimbatore (CJB)</option>
              <option value="TRZ">Tiruchirapalli (TRZ)</option>
              <option value="IXE">Mangalore (IXE)</option>
              <option value="VGA">Vijayawada (VGA)</option>
              <option value="VTZ">Visakhapatnam (VTZ)</option>
              <option value="NAG">Nagpur (NAG)</option>
              <option value="RPR">Raipur (RPR)</option>
              <option value="BHO">Bhopal (BHO)</option>
              <option value="IDR">Indore (IDR)</option>
              <option value="JAI">Jaipur (JAI)</option>
              <option value="UDR">Udaipur (UDR)</option>
              <option value="JDH">Jodhpur (JDH)</option>
              <option value="IXC">Chandigarh (IXC)</option>
              <option value="ATQ">Amritsar (ATQ)</option>
              <option value="SXR">Srinagar (SXR)</option>
              <option value="LEH">Leh (LEH)</option>
              <option value="IXL">Leh (IXL)</option>
              <option value="GAU">Guwahati (GAU)</option>
              <option value="IMP">Imphal (IMP)</option>
              <option value="AJL">Aizawl (AJL)</option>
              <option value="AGX">Agatti (AGX)</option>
              <option value="IXZ">Port Blair (IXZ)</option>
            </select>
            <p className="text-xs text-slate-500">Select your destination city</p>
          </div>
        </div>

        {/* Dates */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="space-y-2">
            <label htmlFor="departDate" className="text-sm font-medium text-slate-700">Departure Date</label>
            <input
              id="departDate"
              name="departDate"
              type="date"
              value={formData.departDate}
              onChange={handleInputChange}
              required
              min={new Date().toISOString().split('T')[0]}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            />
          </div>
          
          {formData.tripType === 'roundTrip' && (
            <div className="space-y-2">
              <label htmlFor="returnDate" className="text-sm font-medium text-slate-700">Return Date</label>
              <input
                id="returnDate"
                name="returnDate"
                type="date"
                value={formData.returnDate}
                onChange={handleInputChange}
                min={formData.departDate || new Date().toISOString().split('T')[0]}
                className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
              />
            </div>
          )}
        </div>

        {/* Passengers */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="space-y-2">
            <label htmlFor="adults" className="text-sm font-medium text-slate-700">Adults</label>
            <select
              id="adults"
              name="adults"
              value={formData.adults}
              onChange={handleInputChange}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            >
              {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((num) => (
                <option key={num} value={num}>
                  {num} {num === 1 ? 'Adult' : 'Adults'}
                </option>
              ))}
            </select>
          </div>

          <div className="space-y-2">
            <label htmlFor="children" className="text-sm font-medium text-slate-700">Children (2-11)</label>
            <select
              id="children"
              name="children"
              value={formData.children}
              onChange={handleInputChange}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            >
              {[0, 1, 2, 3, 4, 5, 6, 7, 8].map((num) => (
                <option key={num} value={num}>
                  {num} {num === 1 ? 'Child' : 'Children'}
                </option>
              ))}
            </select>
          </div>

          <div className="space-y-2">
            <label htmlFor="infants" className="text-sm font-medium text-slate-700">Infants (0-2)</label>
            <select
              id="infants"
              name="infants"
              value={formData.infants}
              onChange={handleInputChange}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            >
              {[0, 1, 2, 3, 4].map((num) => (
                <option key={num} value={num}>
                  {num} {num === 1 ? 'Infant' : 'Infants'}
                </option>
              ))}
            </select>
          </div>
        </div>

        {/* Travel Class */}
        <div className="space-y-2">
          <label htmlFor="fareClass" className="text-sm font-medium text-slate-700">Travel Class</label>
          <select
            id="fareClass"
            name="fareClass"
            value={formData.fareClass}
            onChange={handleInputChange}
            className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
          >
            <option value="Economy">Economy</option>
            <option value="Premium">Premium Economy</option>
            <option value="Business">Business</option>
            <option value="First">First Class</option>
          </select>
        </div>

        {/* Error Alert */}
        {error && (
          <div className="border border-red-200 bg-red-50 p-4 rounded-md">
            <p className="text-red-800 text-sm">{error}</p>
          </div>
        )}

        {/* Search Button */}
        <button
          type="submit"
          disabled={isSearching}
          className="w-full h-12 bg-black hover:bg-slate-800 disabled:bg-slate-400 text-white font-medium rounded-md transition-colors"
        >
          {isSearching ? (
            <div className="flex items-center justify-center space-x-2">
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
              <span>Searching Flights...</span>
            </div>
          ) : (
            'Search Flights'
          )}
        </button>
      </form>

      {/* Debug Information */}
      {debugInfo && (
        <div className="border border-blue-200 bg-blue-50 p-4 rounded-md">
          <h3 className="text-blue-800 font-medium mb-2">Debug Information</h3>
          <div className="text-blue-700 text-sm space-y-1">
            <p><strong>API Base URL:</strong> {debugInfo.apiConfig?.baseUrl}</p>
            <p><strong>Search Path:</strong> {debugInfo.apiConfig?.searchPath}</p>
            <p><strong>API Key Available:</strong> {debugInfo.apiConfig?.hasApiKey ? 'Yes' : 'No'}</p>
            <p><strong>Raw Results Count:</strong> {debugInfo.rawResponseCount}</p>
            <p><strong>Search Criteria:</strong> {JSON.stringify(debugInfo.searchCriteria)}</p>
          </div>
        </div>
      )}

      {/* Search Results */}
      {searchResults.length > 0 && (
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold text-slate-800">
              Flight Results ({searchResults.length} found)
            </h2>
            <div className="text-sm text-slate-600">
              Powered by Trexedia API
            </div>
          </div>
          <FlightResults flights={searchResults} />
        </div>
      )}

      {/* No Results Message */}
      {!isSearching && searchResults.length === 0 && !error && formData.departure && formData.destination && (
        <div className="border border-yellow-200 bg-yellow-50 p-4 rounded-md">
          <p className="text-yellow-800 text-sm">
            No flights found for your search criteria. Please try different dates or destinations.
          </p>
        </div>
      )}
    </div>
  )
}
