'use client'

import { useState } from 'react'
import HotelResults from './HotelResults'

export default function HotelSearchForm() {
  const [searchResults, setSearchResults] = useState<any[]>([])
  const [isSearching, setIsSearching] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [formData, setFormData] = useState({
    destination: '',
    checkIn: '',
    checkOut: '',
    rooms: '1',
    guests: '1'
  })

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSearching(true)
    setError(null)
    
    try {
      const response = await fetch('/api/booking/hotels', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      })

      if (!response.ok) {
        throw new Error('Failed to search hotels')
      }

      const results = await response.json()
      setSearchResults(results.hotels || [])
    } catch (err) {
      setError('Failed to search hotels. Please try again.')
      console.error('Hotel search error:', err)
    } finally {
      setIsSearching(false)
    }
  }

  return (
    <div className="space-y-8">
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Destination */}
        <div className="space-y-2">
          <label htmlFor="destination" className="text-sm font-medium text-slate-700">Destination</label>
          <input
            id="destination"
            name="destination"
            type="text"
            placeholder="City, hotel name, or landmark"
            value={formData.destination}
            onChange={handleInputChange}
            required
            className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
          />
        </div>

        {/* Check-in and Check-out Dates */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="space-y-2">
            <label htmlFor="checkIn" className="text-sm font-medium text-slate-700">Check-in Date</label>
            <input
              id="checkIn"
              name="checkIn"
              type="date"
              value={formData.checkIn}
              onChange={handleInputChange}
              required
              min={new Date().toISOString().split('T')[0]}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            />
          </div>
          
          <div className="space-y-2">
            <label htmlFor="checkOut" className="text-sm font-medium text-slate-700">Check-out Date</label>
            <input
              id="checkOut"
              name="checkOut"
              type="date"
              value={formData.checkOut}
              onChange={handleInputChange}
              required
              min={new Date().toISOString().split('T')[0]}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            />
          </div>
        </div>

        {/* Rooms and Guests */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="space-y-2">
            <label htmlFor="rooms" className="text-sm font-medium text-slate-700">Rooms</label>
            <select
              id="rooms"
              name="rooms"
              value={formData.rooms}
              onChange={handleInputChange}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            >
              {[1, 2, 3, 4, 5].map((num) => (
                <option key={num} value={num.toString()}>
                  {num} {num === 1 ? 'Room' : 'Rooms'}
                </option>
              ))}
            </select>
          </div>
          
          <div className="space-y-2">
            <label htmlFor="guests" className="text-sm font-medium text-slate-700">Guests</label>
            <select
              id="guests"
              name="guests"
              value={formData.guests}
              onChange={handleInputChange}
              className="w-full h-12 px-3 border border-slate-300 rounded-md focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent"
            >
              {[1, 2, 3, 4, 5, 6, 7, 8].map((num) => (
                <option key={num} value={num.toString()}>
                  {num} {num === 1 ? 'Guest' : 'Guests'}
                </option>
              ))}
            </select>
          </div>
        </div>

        {/* Error Alert */}
        {error && (
          <div className="border border-red-200 bg-red-50 p-4 rounded-md">
            <p className="text-red-800 text-sm">{error}</p>
          </div>
        )}

        {/* Search Button */}
        <button
          type="submit"
          disabled={isSearching}
          className="w-full h-12 bg-black hover:bg-slate-800 disabled:bg-slate-400 text-white font-medium rounded-md transition-colors"
        >
          {isSearching ? (
            <div className="flex items-center justify-center space-x-2">
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
              <span>Searching Hotels...</span>
            </div>
          ) : (
            'Search Hotels'
          )}
        </button>
      </form>

      {/* Search Results */}
      {searchResults.length > 0 && (
        <HotelResults hotels={searchResults} />
      )}
    </div>
  )
}
